* {
  font-family: trebuchet ms, verdana, helvetica, arial, sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
}

.main {
  height: 100vh;
  background-color: #2e3440;
  flex-direction: column;
  display: flex;
  position: relative;
}

.main #timers {
  border-top: 1px solid #ffffff1a;
  flex-wrap: wrap;
  margin: 0;
  padding: 16px;
  list-style: none;
  display: flex;
}

.main #timers li {
  cursor: pointer;
  background-color: #ffffff1a;
  border-radius: 4px;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin: 4px;
  padding: 8px 16px;
  transition: opacity .3s;
  display: flex;
}

.main #timers li span {
  color: #fff;
  font-size: 2vh;
  position: relative;
}

.main #timers li span b {
  font-size: .7rem;
  position: absolute;
  top: -4px;
  right: -16px;
}

.main #timers li:hover {
  opacity: .5;
}

.timer-wrapper {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.timer-wrapper span {
  text-align: center;
  color: #fff;
  cursor: pointer;
  font-size: 17vw;
  transition: opacity .3s;
}

.timer-wrapper span:hover {
  opacity: .3;
}

#background-timer {
  z-index: 1;
  pointer-events: none;
  background-color: #ffffff1a;
  transition: bottom .3s;
  position: absolute;
  inset: 0;
}

#information-icon {
  width: 24px;
  height: 24px;
  margin: 8px;
  position: relative;
}

#information-icon img {
  width: 100%;
  cursor: pointer;
}

#information-icon ul {
  background-color: #ccc;
  border-radius: 8px;
  padding: 8px;
  font-size: 1.5vh;
  line-height: 1.1;
  list-style: none;
  transition: top .6s;
  position: absolute;
  top: -1000px;
  left: 28px;
}

#information-icon:hover ul {
  top: 0;
}

.player-content #player-action {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: #0000;
  border: 0;
  margin: 8px;
  padding: 0;
}

.player-content #player-action img {
  width: 100%;
  height: 100%;
}

.gh-link {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.gh-link img {
  width: 100%;
  object-position: center;
}

@media (max-width: 1024px) {
  #timers > li > span b, #information-icon {
    display: none;
  }
}

